.container.isEdit {
    width: 100%;
    max-width: 780px;
    margin: 0 auto;

    .title {
        font-size: $titleSize;

        @media (min-width: $maxMobile) {
            font-size: $titleSizeDesk;
        }
    }

    form {
        padding: 3rem 0;

        label {
            display: block;
            font-size: 1.25rem;
            margin-bottom: .5rem;
        }

        input[type="text"],
        textarea {
            border: none;
            border-radius: .25rem;
            box-shadow: inset 3px 3px 9px rgba(0, 0, 0, .5);
            box-sizing: border-box;
            color: $darkFont;
            padding: .25rem .75rem 0;
            margin-bottom: 3rem;
            width: 100%;

            &:focus {
                outline: none;
            }
        }

        input[type="text"] {
            height: 2rem;
        }

        textarea {
            height: 10rem;
            padding: .5rem .75rem 0;
        }

        input[type="file"] {
            font-size: .75rem;
            height: 2rem;

            &:hover {
                cursor: poiner;
            }
        }

        .tags {
            display: flex;
            flex-flow: row wrap;
            margin: 2rem 0;

            .tag {
                &__choice {
                    display: flex;

                    input[type="checkbox"] {
                        position: relative;
                        right: -1.5rem;
                        top: 7px;
                        z-index: 10;
                    }

                    label {
                        background: orange;
                        border-radius: .25rem;
                        display: inline-block;
                        padding: .5rem .75rem .5rem 1.75rem;
                        user-select: none;
                    }
                }
            }
        }
    }
}


